* {margin: 0;padding: 0;vertical-align: middle;box-sizing: border-box; border: none;}
html,body {width: 100%;height: 100%;}
html {/* scroll-behavior: smooth; */ overflow-y: scroll; overflow-x: hidden;}
body {font: normal 14px/1 'Noto Sans KR', 'Montserrat', 'Roboto';color: #626262;background: #fff}
ul {list-style: none;}
a {color:#626262;text-decoration:none;}
a:link  {text-decoration:none;color:#626262;}
a:hover {text-decoration:none;color:#000;}
a:visited {text-decoration:none;color:#626262;}
a:focus {text-decoration:none;color:#d32b55;}
h1,h2,h3,h4,h5,h6,b,strong {font-weight: normal;}
i, em, address {font-style: normal;}
.blind {display:inline-block;text-indent:-10000px;overflow:hidden;}